
import {Input} from "../../../src"
export default {
    name:"demo-input",
    setup(){
        const inputRef = Input.use.ref("myInput");
        return {
            outerClear:()=>{
                inputRef.value!.methods.clear();
            },
            outerFocus:()=>{
                inputRef.value!.methods.focus(false); // vue文件里面有问题，提示不会消失
            }
        }
    }
}
